import React from 'react';
import ReactDOM from 'react-dom';
import {HashRouter, Route} from 'react-router-dom';
import './index.css';
import './components/table_ronde.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';

// Components
import Header from './components/Header';
import Organisation from './components/Organisation';
import Agenda from './components/Agenda';
import Intervenants from './components/Intervenants';
import Intervenant from './components/Intervenant';
import Videos from './components/Videos';

ReactDOM.render(
<HashRouter>
    <div>
        <Header/>
        <Route exact path='/' component={Organisation}/>
        <Route path='/agenda' component={Agenda}/>
        <Route exact path='/intervenants' component={Intervenants}/>
        <Route path='/intervenant/:id' component={Intervenant}/>
        <Route path='/videos' component={Videos}/>
    </div>
</HashRouter> , 
document.getElementById('root'));
registerServiceWorker();



