import React from 'react';
import {Link} from 'react-router-dom';
import {chronogramme} from '../data/chronogramme';

const generatePannelistes = () => {
  if(chronogramme.panelistes){
    return chronogramme.panelistes.map((item) => {
      return(
        <div key={item.id} className="paneliste">
          <div className="">
            <img src={item.img}  alt=""/>
          </div>
          <div className="text">
            <h4>{item.name} </h4>
            <Link to={`/intervenant/${item.id}`}>Vidéo</Link>
          </div>
        </div>
      );
    })
  }
}

const Videos = () => {
    return (
      <div className="videos">
        <h4 className="header"> Vidéo </h4>
        <iframe 
          src="https://www.youtube.com/embed/xu7FLvsnQFc"
          frameborder="0" gesture="media" allow="encrypted-media" allowfullscreen>
        </iframe>
        <h4 className="header">Vidéo Séquence</h4>
        <div className="panVideos">
           {generatePannelistes()} 
        </div>
      </div>
    );
  }

export default Videos;



