import React from 'react';
import {chronogramme} from '../data/chronogramme';


const generateAgenda = () => {
  if(chronogramme.agenda){
    return chronogramme.agenda.map((item) => {
      return(
          <tr key= {item.id}>
            <td>{item.heure}</td>
            <td>{item.activity}</td>
            <td>{item.intervenant}</td>
            <td>{item.langue}</td>
          </tr>
      )
    })
  }
}

const Agenda = () => {
      return (
      <div className="agenda">
        <p className="App-intro">
            CHRONOGRAMME:
        </p>
        <div className="w3-responsive">
          <table className="w3-table w3-striped w3-border">
            <tbody>
              <tr className="table-header">
                <th>HEURE</th>
                <th>ACTIVITE</th>
                <th>INTERVENANT</th>
                <th>LANGUE</th>
              </tr>
              {generateAgenda()}
            </tbody>
          </table>
        </div>
      </div>
    );
  }

export default Agenda;



