import React from 'react';

const Videos = () => {
    return (
      <div className="organisation">
        <img src="img/banner_table_ronde.jpg" alt=""/>
        <div className="resolutions">
          <h4>Résolutions de la Table Ronde</h4> 
          <p>Malgré sa riche histoire et son potentiel humain, moral, spirituel et matériel immense, la Nation Africaine est,
             depuis de nombreuses décennies, frappée par une crise profonde et multidimensionnelle qui entrave
             l’épanouissement de son peuple et sème le doute quant à la capacité de sa classe dirigeante à faire face
             victorieusement aux périls du moment.</p>
             <p>Cette situation calamiteuse interpelle les élites et exige que soit menée une réflexion globale et approfondie
                pour cerner le mal et apporter les corrections nécessaires devant ouvrir au valeureux peuple africain le chemin
                radieux de la liberté, de l’unité et du bonheur.</p>
              <p>
                A l’examen, on se rend compte que l’élément majeur dans la situation précitée réside dans une connaissance
                insuffisante, sinon une ignorance totale, de notre histoire plusieurs fois séculaire.
                De là découle l’importance capitale du projet de création de la Commission Nationale pour la Réécriture de
                l’Histoire Générale du Sénégal que dirige le Professeur Iba Der THIAM.
              </p>
              <p>
                C’est dans ce sillage que l’Association Ceerno Sileymaani Baal créée en Janvier 2017, a entamé un projet
                ambitieux de vulgarisation de l’immense œuvre de l’érudit Ceerno Silyemaani Baal et de ses compagnons qui
                ont fortement marqué l’histoire de notre peuple. Le premier jalon de ce projet a été de tenir une Table Ronde
                axée sur le thème: <b> «La Révolution du Fouta de 1776 et ses Enseignements; Ceerno Sileymaani BAAL, ses
                Compagnons et l’Almamiyat» </b>.
              </p>
              <p>
                Cette Table Ronde qui a été le creuset d’une réflexion approfondie sur le thème précité, a abouti, à travers les
                différentes interventions de haute portée présentées par neuf éminents professeurs et chercheurs venus de la
                Mauritanie et du Sénégal, aux résultats qui se déclinent ainsi:
              </p>
              <ol>
                <li>
                    <p>Cette Révolution et l’État qu’elle a engendré constituent, dans la sous-région, un repère et un événement
                      d’importance majeure. Il est en effet apparu que la Révolution de 1776 sur les rives du fleuve Sénégal,
                      inspirée et animée par le Martyr Ceerno Sileymaani BAAL et ses compagnons, a permis de libérer les
                      populations du Fouta d’un joug de type colonial avant de fonder un pouvoir éclairé parce que s’inspirant
                      du droit positif, de la liberté, de l’égalité des citoyens et du savoir. A ce titre et de par sa profondeur aux
                      plans politique, moral et philosophique, la Révolution du Fouta de 1776 mérite une attention particulière.
                    </p>
                    <p>Cette Révolution et l’État qu’elle a engendré, ont jeté les bases d’un modèle électif basé sur les principes
                       démocratiques de la bonne gouvernance et de l’État de Droit, indépendamment et bien avant les
                       Révolutions américaines et françaises qui nous sont souvent citées en exemple.
                    </p>
                </li>
                <li>
                    <p>Les différents intervenants ont jugé nécessaire d’inviter les autorités compétentes à revisiter cette partie
                      capitale de notre histoire, à se l’approprier et à la vulgariser en tant que source d’inspiration pour la
                      génération montante.
                    </p>
                </li>
                <li>
                    <p>
                      Les panelistes ainsi que les participants dans leurs interventions, ont fortement recommandé à
                      l’Association Ceerno Silyemaani Baal de concert avec les différents groupements et institutions en rapport
                      avec la vulgarisation de notre histoire, de travailler à la tenue de rencontres et de conférences
                      internationales pour une meilleure diffusion et un plus grand partage des documents historiques
                      disséminés de par le monde. C’est dans ce sillage que nos intellectuels, historiens et chercheurs ont entamé
                      depuis quelques années, un travail remarquable de réhabilitation de notre histoire et de ses véritables
                      héros, de réparation d’injustices notoires et de correction de nombreuses anomalies.
                    </p>
                    <p>
                      Les panélistes ont interpellé l’État du Sénégal sur la nécessité de sortir la Révolution du Fouta de 1776 de
                      l’oubli qui la frappe en donnant à ses dirigeants, à ses héros et surtout aux enseignements de haute portée
                      qu’elle a livrés à l’humanité, la place éminente qu’ils méritent dans notre vie institutionnelle, ce qui
                      pourrait se faire a travers:
                    </p>
                    <ul>
                      <li>
                        <p>
                        l’inclusion à tous les niveaux de son enseignement dans notre système éducatif,
                        </p>
                      </li>
                      <li>
                        <p>
                          la réorientation systémique de notre corpus législatif de façon à l’amener à plus s’inspirer des idéaux et
                        des enseignements à la fois universels et endogènes tirés du message de la Révolution de 1776, etc.
                        </p>
                      </li>
                      <li>
                        <p>
                        une meilleure visibilité des noms des principaux acteurs de la Révolution de 1776 et une plus grande
                        référence aux faits saillants de cet événement majeur, en procédant à de nouvelles dénominations
                        d’institutions et de lieux prestigieux, en leur mémoire.
                        </p>
                      </li>
                    </ul>
                </li>
                <li>
                    <p>
                      Les intervenants ont aussi interpellé les médias publics et privés en les invitant à mieux s’impliquer à
                      l’éducation citoyenne en brisant le silence qui entoure la Révolution du Fouta de 1776 et en soutenant
                      l’effort national de vulgarisation de cet événement historique.
                    </p>
                </li>
                <li>
                    <p>
                      Les panélistes ont lancé un appel aux bonnes volontés, aux institutions et aux collectivités pour qu’elles
                      s’engagent en appuyant toutes les actions tendant à la recherche documentaire, à la collecte, à la
                      conservation, à l’étude, à la réécriture et à la diffusion des actes et objets retraçant TOUS les faits majeurs
                      de notre histoire authentique.
                    </p>
                </li>
              </ol>
              <p>
                En fin l’Association Ceerno Sileymaani BAAL, en se félicitant de la réussite de l’important événement qu’a
                constitué la Table Ronde, remercie très sincèrement toutes les bonnes volontés qui lui ont fait confiance en
                apportant leur appui matériel et moral à l’organisation et singulièrement les panélistes, qui ont gratifié le
                public venu nombreux, de prestations de haute facture.
              </p>
              <p className="withSpan">
                <span>FAIT AU CICES, DAKAR, LE 04 NOVEMBRE 2017</span>
              </p>
        </div>
      </div>
    );
  }

export default Videos;



