import React from 'react';
import {Link} from 'react-router-dom';
import {chronogramme} from '../data/chronogramme';


const Intervenant = (props) => {

    let para = props.match.params.id;
    let {panelistes} = chronogramme;
    let panel = panelistes
                  .filter((filtered) => { return filtered.id.indexOf(para) > -1});
    let panel2 = panel.map(({name, id, img, theme, bio, videoId}) => { 
                  return (
                    <div style={{textAlign: "center"}} key={id}>
                      <Link to='/intervenants'> {"<<<"} Retour</Link>
                      <h4>{name}</h4>
                      <h6>{theme}</h6>
                      <iframe width="560" height="315" 
                        src={`https://www.youtube.com/embed/${ videoId !== "" ? videoId : "xu7FLvsnQFc" }`}
                        frameborder="0" gesture="media" allow="encrypted-media" allowfullscreen>
                      </iframe>
                    </div>
                  )});

    return (
        <div className="paneliste">
          {panel2}
      </div>
    );
  }

export default Intervenant;



